import { Parallax } from 'react-parallax'
import ocean from './oceanSet.jpg'


const LastImage = () => (
    <Parallax 
    className="MainImage" 
    bgImage={ocean} 
    strength={600} 
    renderLayer={percentage => (
        <div
            style={{
                position: 'absolute',
                background: `rgba(255, 255, 255, ${percentage * 0.05})`,
                left: '50%',
                top: '50%',
                transform: "translate(-50%,-50%)",
                width: percentage * 600,
                height: percentage * 300,
            }}
        />
    )}>
        <div className='content'> 
            <h1> Thanks for Visiting! </h1>
        </div>
    </Parallax>
)

export default LastImage;