import { Parallax } from 'react-parallax'
import mountain from './mountains.jpg'


const FirstImage = () => (
    <Parallax 
    blur={{min: -15, max: 15}} 
    className="MainImage" 
    bgImage={mountain} 
    strength={600} 
    renderLayer={percentage => (
        <div
            style={{
                position: 'absolute',
                background: `rgba(255, 255, 255, ${percentage * 0.25})`,
                left: '50%',
                top: '50%',
                borderRadius: "50%",
                transform: "translate(-50%,-50%)",
                width: percentage * 460,
                height: percentage * 460,
            }}
        />
    )}>
        <div className='content'> 
            <h1> Welcome to my Website! </h1>
        </div>
    </Parallax>
)

export default FirstImage;